* {
    box-sizing: border-box;
}

.App {
    margin: 0 auto;
    max-width: 1200px;
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 3fr;
    grid-template-rows: 80px calc(100% - 80px);
    grid-template-areas:
        'h h'
        'n c'
    ;
    font-size: 10px;
}
